body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --h3: Raleway;
  --font-inter: Inter;

  /* font sizes */
  --h3-size: 34px;
  --strong-body-size: 17px;
  --button-size: 20px;
  --font-size-11xl: 30px;
  --subtitle-size: 24px;
  --body-size: 16px;

  /* Colors */
  --color-white: #fff;
  --color-darkslategray-100: #39482f;
  --color-darkslategray-200: rgba(57, 72, 47, 0.5);
  --color-beige: #deeed4;
  --color-dimgray-100: #656262;
  --color-dimgray-200: rgba(101, 98, 98, 0.62);
  --color-gainsboro-100: #dfdfdf;
  --color-gainsboro-200: #d9d9d9;
  --color-plum: #ea96be;
  --color-silver: #c4c4c4;
  --color-whitesmoke: rgba(245, 245, 245, 0.6);
  --color-gray: rgba(255, 255, 255, 0.45);

  /* border radiuses */
  --br-3xs: 10px;
}
